body {
  height: 100vh;
  overflow: hidden;
}
.city_select {
    .city_select_nav{
        border-bottom: 1px solid #eee;
    }
  .city_select_content {
    height: calc(100vh - 45px);
    overflow: hidden;
    background-color: #fff;
    position: relative;
    .city_select_group {
      padding: 10px;
      .group_title {
        color: #aaa;
        font-size: 12px;
        height: 40px;
        display: flex;
        align-items: center;
      }
      .groups_content {
        .city_name {
          height: 40px;
          display: flex;
          align-items: center;
          border-bottom: 1px solid #ddd;
        }
      }
    }
    .letters_content {
        position: absolute;
        top: 5px;
        right: 5px;
        height: 90%;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
      .letters_item {
          width: 20px;
          height: 20px;
          display: flex;
          justify-content: center;
          align-items: center;
      }
      .active {
          background-color: #21b97a;
          border-radius: 50%;
          color: #fff;
      }
    }
  }
}
